.herosection {
  align-self: stretch;
  height: 95vh;
  overflow: hidden;
  flex-shrink: 0;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  padding: var(--padding-64px);
  box-sizing: border-box;
  gap: var(--gap-8px);
  background: url($baseurl + "/assets/uploads/Pngs/AnimeHeroBG.png");
  background-size: cover;
  background-repeat: no-repeat;
  background-position: top;
  max-width: 100%
}



.nav-container {
  align-self: stretch;
  height: 16px;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: space-between;
  gap: var(--gap-xl);
  max-width: 100%;
  font-family: var(--font-pp-neue-machina);
  font-size: var(--font-size-16);
  color: #ffa692;

  a {
    color: #ffa692;
  }
}

nav {
  margin: 0;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  gap: var(--gap-8px);
  max-width: 100%;
  text-align: left;
  text-transform: uppercase;

}



.logo-container {
  align-self: stretch;
  display: flex;
  flex-direction: row;
  flex-grow: 1;
  align-items: center;
  justify-content: center;
  mix-blend-mode: exclusion;

  max-width: 100%;
  flex-shrink: 0;


}

.refuge-logo {
  flex: 1;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: flex-start;
  justify-content: center;
  padding: var(--padding-20px) 0;
  box-sizing: border-box;

  width: 100%;
  justify-content: space-between;


}

.refuge-logo img {

  width: 12vw;
}






.coder-container {
  align-self: stretch;
  display: grid;
  grid-template-columns: 1fr;

  max-width: 100%;


}

.coder {
  display: grid;
  grid-template-columns: 1fr 1fr;
  width: 100%;

}


.codertxt {
  display: grid;
  grid-template-columns: 1fr 1fr;
  width: 100%;
  margin: auto;
  mix-blend-mode: hard-light;
  font-family: var(--font-pp-neue-machina);
  font-size: var(--font-size-16);
  color: #ffa692;

}


.squaobj {

  display: flex;
  flex: 1;
  flex-direction: row;
  align-items: flex-end;
  justify-content: flex-end;
  width: 100%;
}

.component-1 {
  width: 80px;
  height: 80px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  padding: var(--padding-4px);
  box-sizing: border-box;
  gap: 0;
  row-gap: 20px
}





